    _prop = self.Class.DATA_PROPERTIES[prop],
    _valToReturn = self.data[_prop],
    _cloneWith = Array.isArray(_valToReturn) ? [] : {};

    if (_prop === undefined)
    throw "Property does not exist: " + prop;
    if (_typeof(_valToReturn) === "object")
    _valToReturn = bizagi.util.extend(_cloneWith, _valToReturn);

    return _valToReturn;
