/** 
*   Name: Decorator for inboxes
*   Reason: The inboxGrid and normal inbox have independent inherence
*/

$.Class.extend("bizagi.workportal.widgets.inboxes.decorator", {},
{
  /*
  *  Constructor
  */
  init: function init(inboxInstance) {
    var self = this;
    self._addDecorators(inboxInstance);
  },

  /*
     *   Create a proxied multiaction version for service calls
     */
  _addDecorators: function _addDecorators(inboxInstance) {
    var self = this;
    this.internalService = inboxInstance;
    for (var key in self.Class.prototype) {
      if (key == "init" || key == "constructor" || key == "Class" || key == "proxyMethod" || key == "_addDecorators") continue;
      inboxInstance[key] = self[key];
    }
  },

  /**
      * COMMON METHODS FOR INBOX AND INBOXGRID
     */

  /**
         * Show relative menu to configure a smart inbox
         */
  _showSmartInboxMenu: function _showSmartInboxMenu(event) {
    event.stopPropagation();
    var self = this;
    var $parent = $(event.srcElement).closest("div");
    var data = $parent.data();

    var content = $.tmpl(self.smartInboxTemplate, {
      settingName: data.settingname,
      owner: data.owner,
      smartInboxId: data.smartinboxid });


    $(".ui-bizagi-workportal-widget-inbox-common-smartinbox-menucontainer").position({
      my: "right bottom",
      at: "right bottom",
      of: $(event.srcElement) });


    $("li", content).bind("click", function (e) {
      var action = $(e.srcElement).data("action");
      var $parent = $(e.srcElement).closest("ul");
      var data = $parent.data();
      var parameters = {
        settingName: data.settingname,
        owner: data.owner,
        smartInboxId: data.smartinboxid,
        actionName: action };


      switch (action) {
        case "edit":
          self._editSmartInbox(parameters);
          break;
        case "delete":
          self._deleteSmartInbox(parameters);
          break;
        case "share":
          self._shareSmartInbox(parameters);
          break;}

    });

    $(document).on('click.overflow', function (e) {
      $(".ui-bizagi-workportal-widget-inbox-common-smartinbox-menucontainer").empty();
    });

    $(".ui-bizagi-workportal-widget-inbox-common-smartinbox-menucontainer").append(content);

  },

  _editSmartInbox: function _editSmartInbox(parameters) {
    var self = this;
    self.publish("showDialogWidget", {
      selectedInboxData: parameters,
      modalParameters: {
        title: bizagi.localization.getResource("workportal-widget-myfolders-title-edit", "Edit Smart Inbox"),
        width: "800px",
        showCloseButton: false,
        refreshInbox: false },

      showBackButton: false,
      closeVisible: false,
      widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_SMARTINBOX,
      buttons: {} });

  },

  _deleteSmartInbox: function _deleteSmartInbox(parameters) {
    var self = this;
    var content = "<div>" + bizagi.localization.getResource("workportal-process-modeler-deleteConfirmationMessage") + "</div>";

    $(content).dialog({
      title: bizagi.localization.getResource("confirmation-box-title"),
      maximize: false,
      modal: true,
      buttons: [
      {
        text: bizagi.localization.getResource("workportal-widget-queries-confirm-delete"),
        click: function click() {
          $.when(self.dataService.deleteSmartInbox(parameters)).done(function () {
            var storeWidget;
            var grid = bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_INBOX_GRID;
            var homeportal = bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_HOMEPORTAL;
            if ($.isFunction(bizagi.cookie) && bizagi.cookie("bizagiDefaultWidget") !== null && bizagi.cookie("bizagiDefaultWidget") !== homeportal) {
              storeWidget = bizagi.cookie("bizagiDefaultWidget");
            } else {
              storeWidget = grid;
            }

            self.publish("changeWidget", {
              widgetName: storeWidget });

          });
          $(this).dialog("destroy");
        } },

      {
        text: bizagi.localization.getResource("workportal-widget-queries-confirm-cancel"),
        click: function click() {
          $(this).dialog("destroy");
        } }] });



  },

  _shareSmartInbox: function _shareSmartInbox(parameters) {
    var self = this;

    self.publish("showDialogWidget", {
      shareMode: true,
      selectedInboxData: parameters,
      modalParameters: {
        title: bizagi.localization.getResource("workportal-widget-smartinbox-title-share", "Share Smart Inbox"),
        width: 600,
        height: 400,
        showCloseButton: false,
        refreshInbox: false },

      showBackButton: false,
      closeVisible: false,
      widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_SMARTINBOX,
      buttons: {} });

  },

  /**
      * Invokes full summary method and render left pane
      */
  renderSummary: function renderSummary() {
    var self = this;

    var taskState = self.params.taskState;
    var onlyFavorites = "";
    var smartfoldersParameters;
    // Build parameters
    if (taskState == "Favorites") {
      taskState = "all";
      onlyFavorites = "true";
    }
    if (self.params.smartfoldersParameters && self.params.smartfoldersParameters.urlParameters) {
      smartfoldersParameters = self.params.smartfoldersParameters.urlParameters;
    }
    var params = { taskState: taskState || "all", onlyFavorites: onlyFavorites, smartfoldersParameters: smartfoldersParameters };

    // Loads inbox summary
    $.when(self.dataService.getFullInboxSummary(params)).
    done(function (response) {
      self.updateSummary(response.summary.inboxSummary);
      self.resizeLayout();
      // Init with default task state
      if (self.params.selectedSmartInboxId === undefined)
      self.changeTaskState({ taskState: self.params.taskState, processes: response.processes, smartInbox: response.smartInbox, storedQueries: response.storedQueries });else

      self.changeTaskState({ taskState: self.params.taskState, processes: response.processes, smartInbox: response.smartInbox, storedQueries: response.storedQueries, selectedSmartInboxId: self.params.selectedSmartInboxId });
    });
  },

  /**
      * Adds a handler for smart inbox nutton
      */
  configureSmartInbox: function configureSmartInbox() {
    var self = this;
    var content = self.getContent();

    //SmartFolders
    $(".ui-bizagi-wp-app-inbox-smart-inbox-new", content).off().bind("click", function (e) {
      self.publish("showDialogWidget", {
        modalParameters: {
          title: bizagi.localization.getResource("workportal-widget-smartinbox-title-new", "New Smart Inbox"),
          width: "800px",
          showCloseButton: false,
          refreshInbox: false },

        showBackButton: false,
        closeVisible: false,
        widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_SMARTINBOX,
        buttons: {} });


    });
  },


  /*
     *   Configures the view control for the widget
     */
  configureViewControl: function configureViewControl() {
    var self = this;
    var content = self.getContent();
    var eyeIcon = $("#ui-bizagi-wp-app-inbox-bt-eye", content);
    var eyeIconParent = $("#ui-bizagi-wp-app-inbox-bt-eye", content).parent();

    // Bind view handler
    eyeIcon.click(function () {

      bizagi.workportal.desktop.popup.closePopupInstance();
      // Creates popup
      self.popupOpened = false;
      eyeIconParent.addClass("active");
      self.currentPopup = "eyeDetails";
      var offset = "0 0";
      var at = "left bottom";
      var height = 60;
      var width = 129;
      var arrowPosition = {
        left: "64%" };


      var popup = new bizagi.workportal.desktop.popup(self.dataService, self.workportalFacade, {
        sourceElement: "#ui-bizagi-wp-app-inbox-bt-eye",
        offset: offset,
        at: at,
        height: height,
        width: width,
        arrowPosition: arrowPosition });


      // Render popup
      var template = self.getTemplate("inbox-common-header-view");
      popup.render($.tmpl(template));

      // Checks for close signal to change the class again
      $.when(popup.closed()).
      then(function () {
        self.popupOpened = false;
        eyeIconParent.removeClass("active");
      });

      // Details view
      var popupContent = popup.getContent();
      $("#viewDetailsEvent", popupContent).click(function () {
        self.publish("changeWidget", {
          widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_INBOX,
          restoreStatus: true });

        popup.close();
      });

      // Grid view
      $("#viewGridEvent", popupContent).click(function () {
        self.publish("changeWidget", {
          widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_INBOX_GRID,
          restoreStatus: true });

        popup.close();
      });
    });
  },

  /*
     *   Updates inbox summary
     */
  updateSummary: function updateSummary(summary) {
    var self = this;
    var content = self.getContent();

    // Update values
    $(".ui-bizagi-wp-app-inbox-tab#red", content).data("taskState", "Red");
    $(".ui-bizagi-wp-app-inbox-tab#green", content).data("taskState", "Green");
    $(".ui-bizagi-wp-app-inbox-tab#yellow", content).data("taskState", "Yellow");
    $(".ui-bizagi-wp-app-inbox-tab#favourites", content).data("taskState", "Favorites");

    // set number of cases for each category
    //{"inboxSummary":{"All":1151,"Green":85,"Red":1055,"Black":0,"Yellow":11}}
    if (self.params.taskState != "all") {
      $(".ui-bizagi-wp-app-inbox-tab", content).each(function (k, e) {
        if ($(e).data("taskState") == self.params.taskState) {
          $(e).addClass("active");
        } else {
          $(e).removeClass("active");
        }
      });
    }

    $(".ui-bizagi-wp-app-inbox-tab#all", content).find(".toolTip").html(summary.All);
    $(".ui-bizagi-wp-app-inbox-tab#red", content).find(".toolTip").html(summary.Red);
    $(".ui-bizagi-wp-app-inbox-tab#green", content).find(".toolTip").html(summary.Green);
    $(".ui-bizagi-wp-app-inbox-tab#yellow", content).find(".toolTip").html(summary.Yellow);

    var handlerSet = $("#footer #ui-bizagi-wp-app-inbox-tab-container").data("handlerSet") || false;
    if (!handlerSet) {
      $(".ui-bizagi-wp-app-inbox-tab", content).click(function () {
        //remove selected style
        $(".ui-bizagi-wp-app-inbox-tab", content).removeClass("active");
        //set selected style
        $(this).addClass("active");
        var taskStateTo = $(this).data("taskState");

        // get content
        if (self.changeTaskStateDebounce != null) {
          self.changeTaskStateDebounce.cancel();
          self.changeTaskStateDebounce = null;
        }

        self.changeTaskStateDebounce = bizagi.util.debounce2(function () {
          self.changeTaskState({ taskState: taskStateTo, restoreStatus: true });
        }, 200, false);

        self.changeTaskStateDebounce.fire();
      });
      $("#footer #ui-bizagi-wp-app-inbox-tab-container").data("handlerSet", "true");
    }
  },


  /*
     *   Changes the task state and updates the content
     */
  changeTaskState: function changeTaskState(params) {
    var self = this;
    var content = self.getContent();
    var processesContainer = $("#contentProc", content);
    var smartfoldersParameters = "";
    var params = params || {};
    var taskState = params.taskState;

    self._prepareServiceParameters(params);

    // Clear content
    $("#ui-bizagi-wp-app-inbox-tap-selector", content).removeClass().addClass(taskState);
    processesContainer.empty();

    // Prepare services or reuse data from params
    var processesService = !bizagi.util.isEmpty(params.processes) ? params.processes : self.dataService.getAllProcesses({ taskState: self.taskState || "all", onlyFavorites: self.onlyFavorites, smartfoldersParameters: smartfoldersParameters });
    var smartInboxService = !bizagi.util.isEmpty(params.smartInbox) ? params.smartInbox : self.dataService.getAllSmartInbox();

    // Loads processes by task state
    $.when(processesService, smartInboxService).
    then(function (data, smartinboxes) {
      data.smartinboxes = smartinboxes;

      // Sort inbox items
      self._sortInboxItems(data);

      // Render template
      data.hasSmartInboxPermission = bizagi.menuSecurity.SmartInbox || false;
      data = self._prepareDataForRendering(data, data.hasSmartInboxPermission);
      var processesContent = $.tmpl(self.processesTemplate, data);
      if (bizagi.util.isEnableFeature("enableDefaultInbox") === false) {
        $(".ui-bizagi-wp-app-inbox-processes-pin", processesContent).hide();
      }
      processesContent.appendTo(processesContainer);

      // Add sortable behaviour
      self._configureSortableForInboxes(processesContainer, data);

      if (data && data.smartinboxes && data.smartinboxes.length == 0 && data.categories && data.categories.length == 0) {
        processesContainer.hide();
      }

      // Render stored queries
      self._renderStoredQueries(params);

      // Add handlers
      $("li", processesContainer).bind("click", function (event) {self._onInboxItemClick(event, processesContainer, smartfoldersParameters, this);});
      $(".ui-bizagi-wp-app-inbox-processes-start", processesContainer).click(function (event) {self._onFavoritesItemClick(event, this);});
      $(".ui-bizagi-wp-app-inbox-processes-title", processesContainer).click(function () {self._onCategoryItemClick(this);});
      $(".ui-bizagi-wp-app-inbox-processes-pin", processesContainer).click($.proxy(self._onClickPinOrUnpinProcess, self));
      $(".ui-bizagi-wp-app-inbox-processes-smartinbox-settings", processesContainer).click($.proxy(self._showSmartInboxMenu, self));

      // Restore default inbox
      self._restoreDefaultInbox(params, processesContainer);

      // Add scrollbar
      $(processesContainer).bizagiScrollbar();

      // Open first process
      if (data.categories === undefined || data.categories.length == 0) {
        self.changeProcess();
      }

      // Configure smartinbox button
      self.configureSmartInbox();
    });
  },

  _prepareDataForRendering: function _prepareDataForRendering(data, hasSmartInboxPermission) {
    var self = this;

    data = self._addFilteredItems(data, hasSmartInboxPermission);
    data = self._addPinnedItems(data, hasSmartInboxPermission);
    data = self._addAllCasesItem(data);
    data.smartInboxEnable = bizagi.util.isEnableFeature("enableSmartInbox");
    return data;
  },

  _addAllCasesItem: function _addAllCasesItem(data) {
    var self = this;

    if (data.allCases) {
      data.allCases.inboxType = "process";
      var filters = self.getSmartFiltersFromStorage("process", "-1");
      data.allCases.filtered = !bizagi.util.isEmpty(filters) && filters.length > 0 ? true : false;
    }

    return data;
  },

  _addPinnedItems: function _addPinnedItems(data, hasSmartInboxPermission) {
    data.pinned = [];
    var preferences = bizagi.services.preferences().getPreferences();

    $.each(data.categories, function (_, category) {
      $.each(category.workflows, function (_, item) {
        if (preferences.defaultOpenInboxWorkflowId == item.idworkflow) {
          item.pinned = true;
          item.inboxType = "process";
          data.pinned.push(item);
        } else {
          item.pinned = false;
        }
      });
    });

    $.each(data.smartinboxes, function (_, item) {
      if (preferences.defaultOpenInboxSmartInboxId == item.id) {
        item.pinned = true;
        item.inboxType = "smartinbox";
        item.hasSmartInboxPermission = hasSmartInboxPermission;
        data.pinned.push(item);
      } else {
        item.pinned = false;
      }
    });

    return data;
  },

  _addFilteredItems: function _addFilteredItems(data, hasSmartInboxPermission) {
    var self = this;

    $.each(data.smartinboxes, function (i, item) {
      var filters = self.getSmartFiltersFromStorage("smartinbox", item.id);
      item.hasSmartInboxPermission = hasSmartInboxPermission;
      item.filtered = !bizagi.util.isEmpty(filters) && filters.length > 0 ? true : false;
    });
    $.each(data.categories, function (i, category) {
      $.each(category.workflows, function (i, item) {
        var filters = self.getSmartFiltersFromStorage("process", item.idworkflow);
        item.filtered = !bizagi.util.isEmpty(filters) && filters.length > 0 ? true : false;
      });
    });

    return data;
  },

  _prepareServiceParameters: function _prepareServiceParameters(params) {
    var self = this;
    var taskState = params.taskState;

    if (self.params.smartfoldersParameters && self.params.smartfoldersParameters.urlParameters) {
      smartfoldersParameters = self.params.smartfoldersParameters.urlParameters;
    }

    var previewTaskState = self.params.taskState;
    self.params.taskState = taskState;

    // Update internal variable
    if (taskState == "Favorites") {
      self.taskState = "all";
      self.onlyFavorites = "true";
      if (previewTaskState !== taskState) {
        bizagi.referrerParams.page = "1";
      }
    } else {
      self.taskState = taskState;
      self.onlyFavorites = "";
    }
  },

  /* LIVE FILTERS METHODS */
  openLiveFilterDialog: function openLiveFilterDialog(params) {
    var self = this;
    params = params || {};
    self.params.workflowId = self.idWorkflow;
    self.params.smartInboxId = self.smartInboxGuid;

    // Create widget & model
    var model = self._createLiveFilterModel(params);
    var filterWidget = new bizagi.workportal.widgets.smartinbox.filter(self.workportalFacade, self.dataService, self.params);
    filterWidget.setCustomizedColumns(self.liveFilterColumns);

    // Render container
    var $container = $("<div>");
    $.when(filterWidget.render()).
    then(function (filterContent) {
      filterWidget.setModel(model);
      $container.append(filterContent);
    });

    bizagi.dialog;
    $container.dialog({
      maximize: true,
      minimize: false,
      width: 900,
      height: 600,
      title: bizagi.localization.getResource("workportal-widget-smartinbox-live-filters-title", "Filters for"),
      modal: true,
      buttons: [{
        text: bizagi.localization.getResource("workportal-widget-smartinbox-live-filters-cancel", "Cancel"),
        "class": 'bz-btn--secondary',
        click: function click() {
          $(this).dialog("close");
        } },
      {
        text: bizagi.localization.getResource("workportal-widget-smartinbox-live-filters-apply", "Apply"),
        click: function click() {self._onLiveFilterDialogApply($(this), filterWidget);} }],


      close: function close() {
        $(this).dialog('destroy').remove();
      } });

    if (bizagi.localization.isRTL)
    $container.siblings(".ui-dialog-buttonpane").addClass("bz-smartfilters-rtl");
  },

  getSmartFiltersFromStorage: function getSmartFiltersFromStorage(type, id) {
    var filters = [];
    var modelStorage = bizagi.util.getItemLocalStorage("smartfilters");
    var key = type + "-" + id;
    try {
      var serialize = JSON.parse(modelStorage);
      filters = typeof serialize[key] != "undefined" ? JSON.parse(serialize[key]) : filters;
    } catch (e) {
      filters = [];
    }

    $("#contentItemsPaginationInbox .filterColumns").trigger("UPDATEFILTERSCOUNTER", filters.count());

    return filters;
  },

  setSmartFiltersToStorage: function setSmartFiltersToStorage(type, id, value) {
    var modelStorage = bizagi.util.getItemLocalStorage("smartfilters") || "[]";
    var serialize = JSON.parse(modelStorage);
    var key = type + "-" + id;

    serialize[key] = JSON.stringify(value);
    bizagi.util.setItemLocalStorage("smartfilters", JSON.encode(Object.assign({}, serialize)));

    // Update filter mark
    if (type == "smartinbox") {
      var item = $(".ul-processes-list li[data-item-type='smartinbox'][data-guid='" + id + "']");
      if (value.length > 0) {
        $(".ui-bizagi-wp-inbox-filtered", item).css("display", "inline-block");
      } else {
        $(".ui-bizagi-wp-inbox-filtered", item).css("display", "none");
      }
    } else {
      var item = $(".ul-processes-list li[idworkflow='" + (id > 0 ? id : "") + "']");
      if (value.length > 0) {
        $(".ui-bizagi-wp-inbox-filtered", item).css("display", "inline-block");
        $(".ui-bizagi-wp-app-inbox-processes-ncase", item).css("display", "none");
      } else {
        $(".ui-bizagi-wp-inbox-filtered", item).css("display", "none");
        $(".ui-bizagi-wp-app-inbox-processes-ncase", item).css("display", "inline-block");
      }
    }
  },

  isValidFilter: function isValidFilter(smartFilter, customizedColumnsAvailable) {
    var self = this;
    if (smartFilter.column === undefined) return false;
    return $.any(customizedColumnsAvailable, function (column) {return column.fieldFilterName == smartFilter.column;});
  },

  _createLiveFilterModel: function _createLiveFilterModel(params) {
    var self = this;
    var model;
    if (!bizagi.util.isEmpty(self.idWorkflow)) {
      model = self.getSmartFiltersFromStorage("process", self.idWorkflow);
    } else if (!bizagi.util.isEmpty(self.smartInboxGuid)) {
      model = self.getSmartFiltersFromStorage("smartinbox", self.smartInboxGuid);
    } else if (bizagi.util.isEmpty(self.idWorkflow)) {
      model = self.getSmartFiltersFromStorage("process", -1);
    }

    if (params.injectFilter) {
      $.each(self.liveFilterColumns, function (_, value) {
        if (params.injectFilter.column == value.name) {
          params.injectFilter.type = value.type;
        }
      });
      model.push(params.injectFilter);
    }
    self.smartInboxModel.setModel(model);

    return model;
  },

  _onLiveFilterDialogApply: function _onLiveFilterDialogApply(container, filterWidget) {
    var self = this;
    var arrayMessagesErrors = [];
    if (filterWidget.validateFilterForSave(arrayMessagesErrors)) {

      if (!bizagi.util.isEmpty(self.idWorkflow)) {
        self.setSmartFiltersToStorage("process", self.idWorkflow, filterWidget.getModel());
      } else if (!bizagi.util.isEmpty(self.smartInboxGuid)) {
        self.setSmartFiltersToStorage("smartinbox", self.smartInboxGuid, filterWidget.getModel());
      } else {
        self.setSmartFiltersToStorage("process", -1, filterWidget.getModel());
      }

      self.applyLiveFilters(filterWidget.getModel());
      container.dialog("close");
    } else
    {
      $.each(arrayMessagesErrors, function (ind, elem) {
        self.notifier.showErrorMessage(elem);
      });
    }
  },

  /**
      * Encode json filter to base64
      * @param {json} filter 
      */
  encodeLiveFilter: function encodeLiveFilter(filter) {
    var serializeFilter = "";

    try {
      serializeFilter = JSON.encode(filter);
    } catch (e) {
      serializeFilter = JSON.encode("[]");
    }

    var encoded = CryptoJS.enc.Utf8.parse(serializeFilter);

    return CryptoJS.enc.Base64.stringify(encoded);;
  },

  /**
      * Decode base64 filter
      * @param {base64} filter 
      */
  decodeLiveFilter: function decodeLiveFilter(filter) {
    var unserializeFilter = "";
    var decoded = CryptoJS.enc.Base64.parse(filter);
    var jsonFilter = CryptoJS.enc.Utf8.stringify(decoded);

    try {
      unserializeFilter = JSON.parse(jsonFilter);
    } catch (e) {
      unserializeFilter = JSON.parse("[]");
    }

    return unserializeFilter;

  },

  /**
      * Apply a filter and refresh the view
      * @param {json} model 
      */
  applyLiveFilters: function applyLiveFilters(model) {
    var self = this;

    if (!bizagi.util.isEmpty(self.smartInboxGuid)) {
      self.changeProcess({
        type: "smartinbox",
        smartInboxGuid: self.smartInboxGuid,
        liveFilters: self.encodeLiveFilter(model) });

    } else {
      self.changeProcess({
        type: "process",
        idWorkflow: self.idWorkflow,
        liveFilters: self.encodeLiveFilter(model) });

    }

    $("#contentItemsPaginationInbox .filterColumns").trigger("UPDATEFILTERSCOUNTER", model.count());
  },

  /*
     *  Configures the live filters metadata for process headers
     */
  processHeaderLiveFilters: function processHeaderLiveFilters(data) {
    var self = this;
    self.liveFilterColumns = $.map(data.columnTitle, function (column) {
      if (column.fieldName !== "I_idCategory" && column.fieldName !== "I_CurrentUserFullName" &&
      column.fieldName !== "I_UserLocation" && column.fieldName !== "I_CurrentUserName")
      {
        return {
          displayName: column.displayName,
          name: column.fieldFilterName || column.displayName,
          field: column.fieldName,
          type: column.fieldFilterDataType,
          entity: column.entityGuid };

      }
    });
  },

  _configureSortableForInboxes: function _configureSortableForInboxes(container, data) {
    var self = this;
    var sortContainer = $(".ul-processes-list[data-sort-container]", container);
    sortContainer.sortable({
      cursor: "grabbing",
      items: "> li",
      handle: ".ui-bizagi-wp-app-inbox-processes-drag",
      start: function start(e, ui) {
        $(this).attr('data-previndex', ui.item.index());
      },
      update: function update(e, ui) {
        var category = $(e.target).data("sort-container");
        var toIndex = ui.item.index();
        var fromIndex = Number($(this).attr('data-previndex'));
        $(this).removeAttr('data-previndex');
        data = self.changeInboxOrder(data, category, fromIndex, toIndex);
      } });

  },

  _sortInboxItems: function _sortInboxItems(data) {
    var json = bizagi.services.preferences().getPreferences();

    if (json.inboxItemsOrder) {

      // Sort smartinbox items
      if (data.smartinboxes) {
        var orderedItems = json.inboxItemsOrder["smartinbox"];
        if (orderedItems) {
          var notOrderedItems = $.grep(data.smartinboxes, function (item) {return orderedItems.indexOf(item.id) == -1;});
          data.smartinboxes = $.map(orderedItems, function (id) {
            var smartinbox = $.first(data.smartinboxes, function (item) {return item.id == id;});
            return smartinbox;
          });
          if (notOrderedItems && notOrderedItems.length > 0)
          Array.prototype.push.apply(data.smartinboxes, notOrderedItems);
        }
      }

      // Sort categories processes
      if (data.categories) {
        data.categories.map(function (_, i) {
          var guidCategory = data.categories[i].guidCategory;
          var orderedItems = json.inboxItemsOrder[guidCategory];
          var workflows = data.categories[i].workflows;
          if (orderedItems) {
            var notOrderedItems = $.grep(workflows, function (item) {return orderedItems.indexOf(item.idworkflow) == -1;});
            data.categories[i].workflows = $.map(orderedItems, function (id) {
              var workflow = $.first(workflows, function (item) {return item.idworkflow == id;});
              return workflow;
            });
            if (notOrderedItems && notOrderedItems.length > 0)
            Array.prototype.push.apply(data.categories[i].workflows, notOrderedItems);
          }
        });
      }
    }
  },

  _renderStoredQueries: function _renderStoredQueries(params) {
    var self = this;
    var storedQueriesService = !bizagi.util.isEmpty(params.storedQueries) ? params.storedQueries : self.dataService.getStoredQueryFormList();
    $.when(storedQueriesService).then(function (storedQueryForm) {
      if (storedQueryForm.storedQueryForms.length > 0) {
        $.when(
        self.workportalFacade.getWidget("queriesShortcut", {})).
        pipe(function (result) {
          widget = result;
          return widget.render();
        }).done(function () {
          var content = widget.getContent();
          $("#queryFormShortcut").html(content);
        });
      }
    });
  },

  _onInboxItemClick: function _onInboxItemClick(event, processesContainer, smartfoldersParameters, el) {
    var self = this;
    var process = $(el);

    event.stopPropagation();
    $(document).trigger("click.overflow");
    // Remove all selected elements
    $(".selected", processesContainer).removeClass("selected");

    // Add selected class
    process.addClass("selected");

    //if click for human, restore page to 1
    if (event.currentTarget && event.currentTarget.tagName && event.originalEvent) {
      bizagi.referrerParams.page = "1";
    }

    // Change the current process
    var idWorkflow = $("#idWorkflow", process).val();
    var inboxName = $(".ui-bizagi-wp-app-inbox-processes-name", process).text().trim();

    if (process.data('item-type') == "smartinbox") {
      self.changeProcess({
        type: process.data('item-type'),
        smartInboxGuid: process.data('guid'),
        inboxName: inboxName,
        page: bizagi.referrerParams ? bizagi.referrerParams.page : undefined });

    } else
    {
      self.changeProcess({
        idWorkflow: idWorkflow,
        smartfoldersParameters: smartfoldersParameters,
        page: bizagi.referrerParams ? bizagi.referrerParams.page : undefined,
        inboxName: inboxName });

    }
  },

  _onFavoritesItemClick: function _onFavoritesItemClick(e, elmnt) {
    var self = this;
    var options;
    var idWorkflow = $("#idWorkflow", $(elmnt).parent()).val();

    e.stopPropagation();
    $(document).trigger("click.overflow");
    $(elmnt).removeClass("ui-bizagi-wp-app-inbox-processes-start").addClass("ui-bizagi-wp-app-inbox-processes-start-wait");

    // check if its favorite
    if ($(elmnt).parent().find("#isFavorite").val() == "false") {
      options = {
        idObject: $(elmnt).parent().find("#guidWFClass").val(),
        favoriteType: "WFCLASS" };

      $.when(self.dataService.addFavorite(options)).
      then(function (favoritesData) {
        $(elmnt).parent().find("#guidFavorite").val(favoritesData["idFavorites"]);
        $(elmnt).removeClass("bz-icon-star-outline").addClass("bz-icon-star");
        $(elmnt).parent().find("#isFavorite").val("true");
        $(elmnt).removeClass("ui-bizagi-wp-app-inbox-processes-start-wait").addClass("ui-bizagi-wp-app-inbox-processes-start");
        self.updateCountProcess(idWorkflow, true);
      });

    } else {
      options = {
        idObject: $(elmnt).parent().find("#guidFavorite").val(),
        favoriteType: "WFCLASS" };

      $.when(self.dataService.delFavorite(options)).
      then(function (favoritesData) {
        if (favoritesData["deleted"] == "true" || favoritesData["deleted"] == true) {
          $(elmnt).parent().find("#guidFavorite").val("");
          $(elmnt).removeClass("bz-icon-star").addClass("bz-icon-star-outline");
          $(elmnt).parent().find("#isFavorite").val("false");
          $(elmnt).removeClass("ui-bizagi-wp-app-inbox-processes-start-wait").addClass("ui-bizagi-wp-app-inbox-processes-start");
          self.updateCountProcess(idWorkflow, false);
        }
      });
    }

    // Update case list
    if (self.params.taskState == "Favorites" && $(elmnt).parent().hasClass("selected")) {
      $(elmnt).parent().trigger("click");
    }
  },

  _onCategoryItemClick: function _onCategoryItemClick(el) {
    var process = $(el).next("ul");
    var arrow = $(el).find("span");
    var isHidden = process.is(":hidden") ? true : false;
    // Check if element has been hidden
    if (isHidden) {
      process.show();
      arrow.removeClass("Right");
      arrow.addClass("Down");
    } else {
      process.hide();
      arrow.removeClass("Down");
      arrow.addClass("Right");
    }
  },

  _restoreDefaultInbox: function _restoreDefaultInbox(params, processesContainer) {
    var self = this;
    self.params.restoreStatus = params.restoreStatus || self.params.restoreStatus;
    if (params.selectedSmartInboxId == "" || params.selectedSmartInboxId === undefined) {
      bizagi.workportal.widgets.inbox.RESTORE_FROM_BACK_BUTTON(self, processesContainer);

    } else {
      // When a smartinbox was created, then smartInboxGuid is set
      var item = $("li[data-guid='" + params.selectedSmartInboxId + "'][data-item-type='smartinbox']", processesContainer);
      if (item.length > 0) {
        var container = item.parents(".ul-processes-list");
        if (container.css("display") == "none") {
          container.removeClass("nodisplay");
        }
        item.addClass("selected").trigger("click");

      } else {
        bizagi.workportal.widgets.inbox.RESTORE_FROM_BACK_BUTTON(self, processesContainer);
      }
    }
  },

  /**
      * Pin a process
      * @param {JQuery element} containerPin 
      */
  _pinProcessByContainerPin: function _pinProcessByContainerPin() {
    var self = this;
    self.renderSummary();
  },

  /**
      * Unpin a process
      * @param {JQuery element} containerPin 
      */
  _unpinProcessByContainerPin: function _unpinProcessByContainerPin() {
    var self = this;
    self.renderSummary();
  },

  /**
      * OnClick event on Pin icon, pin or unpin actions
      * @param {clickEvent} event 
      */
  _onClickPinOrUnpinProcess: function _onClickPinOrUnpinProcess(event) {
    var self = this;
    event.stopPropagation();
    $(document).trigger("click.overflow");
    var setDefaultInboxInUserPreferences = function setDefaultInboxInUserPreferences(type, id) {
      var modifiedJSON = bizagi.services.preferences().getPreferences();
      if (type == "process") {
        modifiedJSON.defaultOpenInboxWorkflowId = Number(id);
        modifiedJSON.defaultOpenInboxSmartInboxId = undefined;
      } else {
        modifiedJSON.defaultOpenInboxSmartInboxId = id;
        modifiedJSON.defaultOpenInboxWorkflowId = undefined;
      }
      bizagi.services.preferences().overridePreferences(modifiedJSON);
    };

    var containerPin = $(event.target).closest(".container-bz-icon-svg");
    if (containerPin.hasClass("pinned")) {
      self._unpinProcessByContainerPin();
      setDefaultInboxInUserPreferences(-1);
    } else
    {
      self._pinProcessByContainerPin();
      var smartinboxId = $(event.target).closest("li").data("guid");
      var idWorkflow = $(event.target).closest("li").find("#idWorkflow").val();
      setDefaultInboxInUserPreferences(smartinboxId != null ? "smartinbox" : "process", smartinboxId || idWorkflow);
    }
  },

  /**
      * Method that allow us to override the current order of a specific category in the inbox summary
      * @param {clickEvent} event 
      */
  changeInboxOrder: function changeInboxOrder(data, category, from, to) {
    var self = this;
    var json = bizagi.services.preferences().getPreferences();

    json.inboxItemsOrder = json.inboxItemsOrder || {};
    if (category == "smartinbox") {
      var smartinboxes = data.smartinboxes;
      ids = $.map(smartinboxes, function (item) {return item.id;});
      var fromItem = ids.splice(from, 1)[0];
      ids.splice(to, 0, fromItem);
      json.inboxItemsOrder[category] = ids;

    } else {
      var processCategory = $.first(data.categories, function (item) {return item.guidCategory == category;});
      var workflows = processCategory.workflows;
      ids = $.map(workflows, function (item) {return item.idworkflow;});
      var fromItem = ids.splice(from, 1)[0];
      ids.splice(to, 0, fromItem);
      json.inboxItemsOrder[category] = ids;
    }

    bizagi.services.preferences().overridePreferences(json);
    self._sortInboxItems(data);
    return data;
  } });