/*
 *   Name: Bizagi Workportal Project Cancel Case
 *   Author: Yesid Alberto Ochoa Luque
 */
bizagi.workportal.widgets.widget.extend("bizagi.workportal.desktop.widgets.project.cancelCase", {}, {
  /*
                                                                                                      *   Constructor
                                                                                                      */
  init: function init(workportalFacade, dataService, params) {
    var self = this;
    // Call base
    self._super(workportalFacade, dataService, params);

    self.notifier = bizagi.injector.get('notifier');
    params = params || {};
    //Load templates
    self.loadTemplates({
      "project-cancel-case-wrapper": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.cancelCase").concat("#project-caseState-cancel-case-wrapper"),
      "project-cancel-case-wrapper-error": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.cancelCase").concat("#project-caseState-cancel-case-wrapper-error") });

  },

  /*
     * Renders the template defined in the widget
     */
  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate("project-cancel-case-wrapper");
    self.content = template.render({});
    self.defineButtons();
    return self.content;
  },
  /*
     * Define modal buttons
     */
  defineButtons: function defineButtons() {
    var self = this;
    self.params.buttons = [{
      text: bizagi.localization.getResource("workportal-general-word-no"),
      "class": "bz-cancel-case__btn biz-wp-button-secondary",
      click: function click() {
        $(this).dialog('close');
      } },
    {
      text: bizagi.localization.getResource("workportal-general-word-yes"),
      "class": "bz-cancel-case__btn",
      click: function click() {self.cancelCaseRequest();} }];

  },
  /**
      * Execute request to cancel case
      */
  cancelCaseRequest: function cancelCaseRequest() {
    var self = this,
    reason = "",
    $loader = $("#bz-wp-cancelcase-wrapperload", self.content);
    reason = $("textarea#cancel-case-reason").val();
    var params = {
      idWorkitem: self.params.idWorkitem,
      action: "abort",
      cases: JSON.stringify({
        cases: [{ idCase: self.params.idCase }] }),

      reason: reason };


    bizagi.showErrorAlertDialog = false;

    if (reason != "") {
      $loader.show();
      $.when(self.dataService.cancelCase(params)).done(function (result) {
        if (result.response[0].deleted) {
          $loader.hide();
          self.notifier.showSucessMessage(bizagi.localization.getResource("workportal-widget-cancel-case-the-case-was-cancelled-successfully"));
          $('.ui-dialog-content').dialog('close');
          bizagi.injector.get("bizagi.workportal.widgets.navigator", {}).goToBack();
        } else {
          $loader.hide();
          bizagi.showToastError(bizagiConfig.environment, result.response[0].idCase);
        }
      }).fail(function (error) {
        var message_error = error.status + " - " + error.statusText;
        bizagi.showToastError(bizagiConfig.environment, message_error);
        $loader.hide();
        bizagi.log(error);
      });
    } else {
      $("textarea#cancel-case-reason").css("border-color", "#F50E0E");
      $(".bz-cancel-case__help-text").text(bizagi.localization.getResource("workportal-widget-cancel-case-the-cancellation-reason-is-mandatory")).css("color", "#F50E0E");
      self.showTextIsRequired();
    }
  },

  /**
      * If the text area is empty, it shows alert that the text is required
      */
  showTextIsRequired: function showTextIsRequired() {
    $('textarea#cancel-case-reason').bind('input propertychange', function () {
      $("textarea#cancel-case-reason").css("border-color", "#F50E0E");
      $(".bz-cancel-case__help-text").text(bizagi.localization.getResource("workportal-widget-cancel-case-the-cancellation-reason-is-mandatory")).css("color", "#F50E0E");
      if (this.value.length) {
        $("textarea#cancel-case-reason").css("border-color", "#");
        $(".bz-cancel-case__help-text").text(bizagi.localization.getResource("workportal-widget-cancel-case-max-characters")).css("color", "##757575");
      }
    });
  } });


bizagi.injector.register('bizagi.workportal.desktop.widgets.project.cancelCase', ['workportalFacade', 'dataService', bizagi.workportal.desktop.widgets.project.cancelCase], true);